let window = document.documentElement.clientWidth;


import Swiper, {Autoplay, Navigation, Pagination, EffectCoverflow, } from 'swiper';
if(window<='768'){
  Swiper.use([Autoplay,Pagination,EffectCoverflow, Autoplay]);
  const swiper = new Swiper('.gallery-swiper', {
    slidesPerView: 1,
    spaceBetween: 50,
    loop: true,
    preloadImages: false,
    speed: 1100,
    observer: true,
   observeParents: true,
    lazy: {
     loadPrevNext: true,
     loadPrevNextAmount: 3,
   },

   autoplay :{
     delay: 2000,
   },



     pagination: {
     el: '.swiper-pagination',
     type: 'bullets',
     clickable: true,
   },

  });
}else{

  Swiper.use([Autoplay,Pagination,EffectCoverflow, Autoplay]);
  const swiper = new Swiper('.gallery-swiper', {
    slidesPerView: 3,
    spaceBetween: 30,
    loop: true,
    preloadImages: false,
    speed: 1100,
    observer: true,
   observeParents: true,
    lazy: {
     loadPrevNext: true,
     loadPrevNextAmount: 3,
   },

   autoplay :{
     delay: 2000,
   },
   effect: 'coverflow',
   coverflowEffect: {
     rotate: 30,
     slideShadows: false,
   },


     pagination: {
     el: '.swiper-pagination',
     type: 'bullets',
     clickable: true,
   },

  });

}


 Swiper.use([Navigation]);
 const slider = new Swiper('.comment_swiper', {
   slidesPerView: 1,
   spaceBetween: 500,
   loop: true,
   preloadImages: true,
   speed: 1000,
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  });



